import React, {Component} from 'react';
import {Grid, Cell} from "react-mdl";
import Education from './Education';
import Experience from './Experience';

class Resume extends Component {
    render() {
        return(
            <Grid>
                {/*Left Cell*/}
                <Cell col={4}>
                    <div style={{textAlight: 'center'}}>
                        <img
                            src={"https://cdn2.iconfinder.com/data/icons/avatars-vol-1-glyph/64/glyph-avatar-beard-shades-cool-512.png"}
                            alt={"avatar"}
                            style={{height: '200px'}}
                        />
                    </div>

                    <h2 style={{paddingTop: '2em'}}>Tye Gallagher</h2>
                    <h4 style={{color: 'grey'}}>Programmer</h4>
                    <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}></hr>
                    <p>Dummy text</p>
                    <hr style={{borderTop: '3px solid #833fb2', width: '50%'}}></hr>
                    <h5>Address</h5>
                    <p>3229 Covina St, New Port Richey, Fl 34655-3404, USA</p>
                    <h5>Phone</h5>
                    <p>(575) 791-2343</p>
                    <h5>Email</h5>
                    <p>tyeg@outlook.com</p>
                    <h5>Web</h5>
                    <p>mywebsite.com</p>
                </Cell>

                {/*Right cell*/}
                <Cell className={"resume-right-col"} col={8}>
                    <h2>Education</h2>
                    <Education
                        startYear={2012}
                        endYear={2019}
                        schoolName={"Eastern New Mexico University"}
                        schoolDescription={"Graduated with a bachelor's of science in computer science with a focus on data processing and analysis"}
                    />
                    <hr style={{borderTop: '3px solid #e22947'}}/>
                    <h2>Work History</h2>
                    <Experience
                        startMonth={"April"}
                        startYear={2017}
                        endMonth={"Present"}
                        endYear={2019}
                        jobName={"IT Technician"}
                        jobDescription={"Created tools that were distributed to all Seaworld parks & Entertainment locations nationwide to automate reporting procedures." +
                        "Responsible for maintaining, auditing, and troubleshooting over 1000 devices used by over 5000 users"}
                    />
                </Cell>
            </Grid>


        )
    }
}

export default Resume